import { Button, TextField } from '@mui/material';
import Paper from '@mui/material/Paper';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import * as React from 'react';
import { useParams } from 'react-router-dom';
import { DataContext } from '..';

export default function DetailsTable() {
  const dataArray = React.useContext(DataContext);
  const { id } = useParams();
  const [key, setKey] = React.useState('');
  const [data, setData] = React.useState([]);

  const setTableData = () => {
    setData(tableData);
  };

  let tableData = dataArray[1].filter((domainData) => {
    return domainData.id.toString() === id;
  });

  const indexOfTableData = dataArray[1].indexOf(tableData);

  const keys = [];
  React.useEffect(() => {
    keys.push(key);
  }, [key]);

  const onFilter = () => {
    const urls = tableData[0].urls;
    const filteredUrls = urls.filter(
      (urlData) =>
        !urlData.url
          .toLowerCase()
          .includes(keys.map((key) => key.toLowerCase()))
    );
    tableData[0].urls = filteredUrls;
    dataArray[1][indexOfTableData] = tableData;
    setKey('');
    return setTableData();
  };

  React.useEffect(() => {
    if (tableData) {
      setTableData();
    }
  }, []);

  return (
    <>
      <div
        style={{
          display: 'flex',
          flexDirection: 'row',
          alignItems: 'center',
          justifyContents: 'center',
          width: '500px',
          margin: '0 auto',
        }}>
        <TextField
          sx={{ margin: '0 auto', minWidth: '350px' }}
          id='outlined-basic'
          label='Filter URLs'
          variant='outlined'
          value={key}
          onChange={(e) => setKey(e.target.value)}
        />
        <Button
          sx={{ height: '54px', width: '100px' }}
          variant='contained'
          onClick={() => {
            if (key === '') {
              alert('Please input key!!!');
            } else {
              onFilter();
            }
          }}>
          Filter
        </Button>
      </div>
      <br />
      <hr />
      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 650 }} aria-label='simple table'>
          <TableHead sx={{ background: '#B1F0ED', fontWeight: 'bold' }}>
            <TableRow>
              <TableCell>URL</TableCell>
              <TableCell align='right'>STATUS</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {data[0]?.urls.map((row, index) => (
              <TableRow
                key={index}
                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
                <TableCell
                  style={{
                    color: 'royalblue',
                    fontWeight: 'bold',
                  }}
                  component='th'
                  scope='row'>
                  {row.url}
                </TableCell>
                {row.status === 200 && (
                  <TableCell
                    sx={{ color: 'green', fontWeight: 'bold' }}
                    align='right'>
                    {row.status}
                  </TableCell>
                )}
                {row.status === 404 && (
                  <TableCell
                    sx={{ color: 'red', fontWeight: 'bold' }}
                    align='right'>
                    {row.status}
                  </TableCell>
                )}
                {row.status === 301 && (
                  <TableCell
                    sx={{ color: 'orange', fontWeight: 'bold' }}
                    align='right'>
                    {row.status}
                  </TableCell>
                )}
                {row.status !== 200 &&
                  row.status !== 301 &&
                  row.status !== 404 && (
                    <TableCell
                      sx={{ color: 'royalblue', fontWeight: 'bold' }}
                      align='right'>
                      {row.status}
                    </TableCell>
                  )}
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </>
  );
}
