import { Button } from '@mui/material';
import React, { useEffect } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { HashLoader } from 'react-spinners';
import { DataContext } from '..';
import DetailsTable from '../components/DetailsTable';

const Details = () => {
  const dataArray = React.useContext(DataContext);

  const navigate = useNavigate();
  const [loading, setLoading] = React.useState(true);

  const { id } = useParams();

  useEffect(() => {
    let tableData = dataArray[1].filter((domainData) => {
      return domainData.id.toString() === id;
    });
    if (tableData && tableData[0] && tableData[0].urls !== []) {
      setLoading(false);
    }
  }, []);

  return (
    <div>
      <Button
        variant='contained'
        component='label'
        sx={{ margin: '20px' }}
        onClick={() => {
          navigate('/result');
        }}>
        Back to results
      </Button>
      <br />
      {loading ? <HashLoader color='#36d7b7' size={200} /> : <DetailsTable />}
    </div>
  );
};

export default Details;
